module.exports = {
  // 空
  val_empty(v, txt) {
    if ((v == null) || (v.trim() == '')) {
      return txt + '不应为空。'
    }
    return true
  },
  val_char(v, txt) {
    let char = /[`~!@#$%^&*(“”‘’'、～·！—_+|=;？，。\\)<>?:"{},.\/;'[\]]/;
    if (char.test(v)) {
      return txt + '中不應有特殊字符。'
    }
    return true
  },
  val_char_price(v, txt) {
    let char = /[`~!@#$%^&*(“”‘’'、～·！—_+|=;？，。\\)<>?:"{},\/;'[\]]/;
    if (char.test(v)) {
      return txt + '中不應有除了.之外的特殊字符。'
    }
    return true
  },
}


