<template>
  <div class="fixed-plus-btn hand"></div>
</template>

<script>
export default {
  name: 'PlusFixed'
}
</script>

<style lang="sass">
@import "@/common/sass/layout.sass"

.fixed-plus-btn
  background: url('../../assets/icon/new.png')
  background-repeat: no-repeat
  background-size: calc( 48px + 1vw )
  background-position: 0px

.fixed-plus-btn
  border-radius: 100%
  position: fixed
  right: 0
  bottom: $horizon
  margin-right: $horizonXXL
  width: calc( 48px + 1vw )
  height: calc( 48px + 1vw )

</style>


