<template>
  <div class="modal-form-wrapper">
    <form>
      <div class="form-group">
        <label for="num">货号</label>
        <input type="text" class="form-control" id="num" placeholder="货号" v-model="num">
        <small id="numTip" class="form-text text-muted">{{ tip_num }}</small>
      </div>
      <div class="form-group">
        <label for="named">描述</label>
        <input type="password" class="form-control" id="named" placeholder="描述" v-model="named">
        <small id="numTip" class="form-text text-muted">{{ tip_named }}</small>
      </div>
      <div class="form-group row">
        <div class="col-sm-4">
          <label for="price">零售价</label>
          <input type="password" class="form-control" id="price" placeholder="价格" v-model="price">
        <small id="numTip" class="form-text text-muted">{{ tip_price }}</small>
        </div>
        <div class="col-sm-4">
          <label for="unit">单位</label>
          <select class="form-control" id="unit" v-model="unit">
            <option v-for="u in units" :key="u.flag" :value="u.flag">{{ u.named }}</option>
          </select>
        </div>
        <div class="col-sm-4">
        </div>
      </div>
      <div class="form-group">
        <label for="named">标签</label>
        <div class="tag-wrapper">
          <div class="tag tag-def d-inline hand" v-for="t in tags" :key="t.named" :class="{ 'tag-active': inList(t.id, select) }" @click="tagChoise(t.id)">
            {{ t.named }}
          </div>
          <div class="tag tag-plus hand">
            加
          </div>
        </div>
        <small id="numTip" class="form-text text-muted">{{ tip_tag }}</small>
      </div>
    </form>
  </div>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'

import service from '@/service'

export default {
  name: 'FreightPlusForm',
  data() {
    return {
      select: [ 1, 4 ],
      tags: service.tags,

      //
      num: '',
      tip_num: '',

      named: '',
      tip_named: '',

      price: '',
      tip_price: '',
      
      unit: 0,

      tip_tag: ''
    }
  },
  computed: {
    units() {
      return [
        { flag: 0 },
        { flag: 1 },
        { flag: 2 },
        { flag: 3 },
        { flag: 4 },
      ].map((e) => {
        const res = view.ser_unit(e.flag)
        return {
          flag: e.flag,
          named: res
        }
      })
    }
  },
  methods: {
    inList(n, L) {
      return tool.inList(n, L)
    },
    tagChoise(id) {
      const index = tool.indexList(id, this.select)
      if (index >= 0) {
        console.log(this.select.length)
        if (this.select.length <= 1) {

          tool.showToast('提示：请至少选择一个标签。')
          console.log('标签')
        } else {
          this.select.splice(index, 1)
        }
      } else {
        this.select.push(id)
      }
    },
    tipClose(area) {
      setTimeout(() => {
        if (area == 'num') {
          this.tip_num = ''
        }
        else if (area == 'named') {
          this.tip_named = ''
        }
        else if (area == 'price') {
          this.tip_price = ''
        }
      }, 4200)
    },
    // 
    tipForm(res, area) {
      let show = ''
      if (res != true) {
        show = res
      }
      // 
      if (area == 'num') { this.tip_num = show }
      else if (area == 'named') { this.tip_named = show }
      else if (area == 'price') { this.tip_price = show }

      this.tipClose(area)
    },
    validateForm() {
      let res = validate.val_empty(this.num, '货号')
      if (res == true) {
        res = validate.val_char(this.num, '货号')
        this.tipForm(res, 'num')
      } else { this.tipForm(res, 'num') }
      
      res = validate.val_empty(this.named, '描述')
      if (res == true) {
        res = validate.val_char(this.named, '描述')
        this.tipForm(res, 'named')
      } else { this.tipForm(res, 'named') }

      res = validate.val_empty(this.price, '零售价')
      if (res == true) {
        res = validate.val_char_price(this.price, '零售价')
        this.tipForm(res, 'price')
      } else { this.tipForm(res, 'price') }

      return true
    },
    submitForm() {
      const submit = this.validateForm()
    }
  },
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"


</style>


