<template>
  <div class="content-wrapper">
    <div class="row mt-1">
      <div class="col-sm-12">
        <div class="page-title fw-b text-main">菜品列表</div>
      </div>
    </div>
    <div class="row mt-2 d-none">
      <div class="col-sm-3">
        <div class="input-group">
          <div class="input-group-prepend">
            <div class="input-group-text icon-input-wrapper" id="btnGroupAddon2">
              <i class="fa fa-th-large"></i>
            </div>
          </div>
          <select class="form-control" placeholder="标签筛选">
            <option value="0">标签筛选</option>
            <option v-for="t in tagList" :key="t.id" :value="t.id">{{ t.named }}</option>
          </select>
        </div>
      </div>
      <div class="col-sm-7">
        <div class="form-goup"> 
          <input type="text" class="form-control search-input" aria-label="freight-search">
        </div>
      </div>
      <div class="col-sm-2">
        <button class="btn btn-main search-right-btn">搜索</button>
      </div>
    </div>

    <div class="row mt-3">
      <div class="col-sm-12 data-panel-wrapper">
        <freight-view-data-table></freight-view-data-table>
      </div>
    </div>

    <i @click="plusView">
      <plus-fixed></plus-fixed>
    </i>

    <freight-plus></freight-plus>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
import service from '@/service'
import FreightPlus from '../Plus/FreightPlus.vue'
import FreightViewDataTable from './FreightViewDataTable.vue'
import PlusFixed from '../../../components/Option/PlusFixed.vue'

export default {
  components: { FreightViewDataTable, FreightPlus, PlusFixed },
  name: 'FreightView',
  data() {
    return {
      tagList: service.tags
    }
  },
  methods: {
    plusView() {

      tool.showModal('freightPlus')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.icon-input-wrapper
  background: $primary
  color: white
  padding-left: $horizon
  padding-right: $horizon
  border-radius: $horizonS

.search-right-btn
  padding-left: 3vw
  padding-right: 3vw

.search-input-icon *
  background: none

.search-input
  background: url('../../../assets/icon/search.png')
  background-repeat: no-repeat
  background-size: 16px
  background-position: 10px
  background-position-x: $horizon
  padding-left: 48px

</style>


