<template>
  <div class="data-panel def-shadow">

    <div class="row ">
      <div class="col-sm-12">
        <div class="table-header h4 d-inline float-left text-accent">全部</div>

        <!-- div class="btn-group mr-2 float-right table-action" role="group" aria-label="First group">
          <button type="button" class="btn btn-secondary">
            <i class="fa fa-star fa-pos-fix-l"></i>
          </button>
          <button type="button" class="btn btn-secondary">
            <i class="fa fa-star-o fa-pos-fix-l"></i>
          </button>
        </div -->

      </div>
    </div>

    <div class="row">
      <div class="col-sm-12">

        <table class="table data-table br-t-none">
          <thead class="br-t-none">
            <tr class="br-t-none">
              <th scope="col" class="text-center">货号</th>
              <th scope="col">描述</th>
              <th scope="col" class="text-center">单位</th>
              <th scope="col">标签</th>
              <th scope="col" class="text-center">建议零售价</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="f in data" :key="f.id">
              <th scope="row" class="text-center">{{ f.num }}</th>
              <td>{{ f.named }}</td>
              <td class="text-center">{{ unit(f.unit) }}</td>
              <td>
                <span v-for="t in f.tag" :key="t.named" class="tag tag-table" :style="{ 'background': t.color }">{{ t.named }} </span>
              </td>
              <td class="text-center">
                $ {{ f.price }}
              </td>
              <!-- td>
                <i @click="option(f.id, 'edit')">
                  <pencil></pencil>
                </i>
                <i @click="option(f.id, 'trash')">
                  <remove></remove>
                </i>
              </td -->
            </tr>
          </tbody>
        </table>

      </div>
    </div>
    

    <freight-trash></freight-trash>
  </div>
</template>

<script>

import view from '@/common/es/view'
import tool from '@/common/es/tool'

import service from '@/service'

import Pencil from '../../../components/Option/Pencil.vue'
import Remove from '../../../components/Option/Remove.vue'
import FreightTrash from '../Trash/FreightTrash.vue'

export default {
  components: { Pencil, Remove, FreightTrash },
  name: 'FreightViewDataTable',
  data() {
    return {
      data: service.freights
    }
  },
  methods: {
    unit (u) {
      return view.ser_unit(u)
    },
    option(id, option) {
      if (option == 'edit') {
        alert(`编辑 ID = ${id} 的菜品。`)
      }
      else if (option == 'trash') {
        tool.showModal('freightTrashModal')
        // alert(`删除 ID = ${id} 的菜品。`)
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.table-action button
  background: white
  color: $accent

.data-table
  border-color: $accentLine !important


</style>


